    /**
     * 
     * @param {*} value --
     */
    resetPkorgRef(value){
        this.props.renderItem('form', memberFormId, 'value', null); // jsonmoduleIdid
        let meta = this.props.meta.getMeta();
        meta[memberFormId].items.map((obj)=>{
            if(obj.attrcode === 'pk_org'){
                obj.refcode = null;
                obj.refName= null;
                obj.isShowUnit = false;
                switch (value) {
                    case '1':// 1
                        obj.refcode = 'uapbd/refer/psninfo/PsndocTreeGridRef/index.js';
                        obj.refName=this.state.json['10100BRS-000045'];/* */
                        obj.isShowUnit = true;
                        unitConf.placeholder = this.state.json['10100BRS-000048'];/*  */
                        obj.unitProps=unitConf;
                    break
                    case '2'://
                        obj.refcode = 'uapbd/refer/org/PostDefaultGridRef/index.js';
                        obj.refName=this.state.json['10100BRS-000046'];/* */
                        obj.isShowUnit = true;
                        unitConf.placeholder = this.state.json['10100BRS-000048'];/*  */
                        obj.unitProps=unitConf;
                    break
                    case '3'://+
                        obj.refcode = 'uapbd/refer/org/JobGridRef/index.js';
                        obj.refName=this.state.json['10100BRS-000047'];/* */
                    break
                }
            }
            if(obj.attrcode === 'pk_dept'){//
                switch (value) {
                    case '3':
                        obj.visible = true;
                    break
                    default :
                        obj.visible = false;
                    break
                }
            }
        })
        this.props.meta.setMeta(meta);
    }

